<script setup lang="ts">
export interface BaseBreadcrumbsItem {
  title: string;
  to: string;
  active?: boolean;
}
export interface BaseBreadcrumbsProps {
  items: BaseBreadcrumbsItem[];
}

const props = defineProps<BaseBreadcrumbsProps>();
const isLast = (index: number) => index === props.items.length - 1;
const breadcrumbTag = (item: BaseBreadcrumbsItem) => item.active ? "span" : item.to ? "nuxt-link" : "button";
</script>

<template>
  <nav v-if="props.items" class="breadcrumb">
    <div v-for="(item, index) in props.items" :key="item.title" class="breadcrumb__item text-caption-medium" :class="{ breadcrumb__item_active: item.active }">
      <BaseButton :title="item.title" :name="`breadcrumb-item-${index}`" :tag="breadcrumbTag(item)" :to="item.to" type="secondary" link>
        {{ item.title }}
      </BaseButton>
      <IconChevronRight v-if="!isLast(index)" :height="6" :width="6" class="mx-2 text-secondary" />
    </div>
  </nav>
</template>

<style lang="scss">
.breadcrumb {
    @apply flex items-center;
}

.breadcrumb__item {
    @apply flex items-center relative;
    &.breadcrumb__item_active *:hover {
        @apply no-underline cursor-auto;
    }
}
</style>
